<template>
  <div class="home">
    <b-row>
      <b-col v-if="false" class="text-center">
        <div>{{note}}</div>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="2" class="text-nowrap">{{str_namefarm}}</b-col>
      <b-col>
        <b-form-input v-model="name_farm" placeholder="กรอกชื่อแปลง"></b-form-input>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="2" class="text-nowrap">{{str_variety}}</b-col>
      <b-col>
        <b-form-input v-model="variety" placeholder="กรอกชนิดต้นอ้อย"></b-form-input>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="text-nowrap" v-for="(item, index) in items">
        <label class="custom-file-upload btn btn-primary" style="border: 1px solid black;">
          <input
            :disabled="uploading"
            type="file"
            accept="image/*"
            v-on:change="handleFileUpload(index)"
            ref="items"
          />
          {{item.str_upload}}
        </label>
        {{item.e}}
      </b-col>
    </b-row>
    <b-row>
      <b-col class="text-center">
        <b-button
          variant="outline-primary"
          :disabled="disUpload"
          @click="clickUpload"
        >{{str_upload}}</b-button>
      </b-col>
    </b-row>
    <br />
    <b-row>
      <b-col class="text-center">
        <b>{{str_sampledata}}</b>
      </b-col>
    </b-row>
    <b-row>
      <b-col class="text-center">
        <b-button :disabled="uploading" variant="warning" @click="test1">{{str_sample1}}</b-button>&nbsp;
        <b-button :disabled="uploading" variant="warning" @click="test2">{{str_sample2}}</b-button>
      </b-col>
    </b-row>
    <br />
    <b-row>
      <b-col class="text-center">
        <b-progress :value="uploadPercentage" :max="100" height="3px" :animated="animate" />
      </b-col>
    </b-row>
    <b-row v-if="true">
      <b-col class="text-center">
        <div id="status" class="ex2" align="left" style="white-space: pre;" v-html="str_result"></div>
        <br />
      </b-col>
    </b-row>
    <b-row v-show="width <1008">
      <b-table striped hover :items="result" stacked>
        <template v-slot:cell(farm)="data">
          <b-button @click="next(data.item.farm)">คำนวณ</b-button>
        </template>
        <template v-slot:cell(rgb)="data">
          <b-button @click="next(data.item.rgb)">RGB</b-button>
        </template>
        <template v-slot:cell(name)="data">
          <b-form-input v-model="data.item.name"></b-form-input>
        </template>
        <template v-slot:cell(variety)="data">
          <b-form-input v-model="data.item.variety"></b-form-input>
        </template>
        <template v-slot:cell(delete)="data">
          <b-button variant="danger" @click="clickDeleteFarm(data.item.delete)">Delete</b-button>
        </template>
      </b-table>
    </b-row>
    <b-row v-show="width >=1008">
      <b-table striped hover :items="result">
        <template v-slot:cell(farm)="data">
          <b-button @click="next(data.item.farm)">คำนวณ</b-button>
        </template>
        <template v-slot:cell(rgb)="data">
          <b-button @click="next(data.item.rgb)">RGB</b-button>
        </template>
        <template v-slot:cell(name)="data">
          <b-form-input v-model="data.item.name"></b-form-input>
        </template>
        <template v-slot:cell(variety)="data">
          <b-form-input v-model="data.item.variety"></b-form-input>
        </template>
        <template v-slot:cell(delete)="data">
          <b-button variant="danger" @click="clickDeleteFarm(data.item.delete)">Delete</b-button>
        </template>
      </b-table>
    </b-row>
    <!-- <b-row>
      <b-col class="text-center">
        <b-button variant="outline-primary" @click="clickGet">Get all farms</b-button>
      </b-col>
    </b-row>-->
    <b-row>
      <b-col class="text-center">
        <b-button variant="outline-primary" @click="clickDelete">Delete all farm</b-button>
      </b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  name: "home",
  data: function() {
    return {
      width: 0,
      name_farm: "แปลงเกษตรไทย",
      variety: "UT84-12",
      id_farm: "",
      id_farm_new: "",
      id_farm_old: [],
      items: [
        { e: "", message: "r", str_upload: "", file: "" },
        { e: "", message: "g", str_upload: "", file: "" },
        { e: "", message: "b", str_upload: "", file: "" },
        { e: "", message: "nir", str_upload: "", file: "" },
        { e: "", message: "re", str_upload: "", file: "" },
        { e: "", message: "dsm", str_upload: "", file: "" },
        { e: "", message: "dtm", str_upload: "", file: "" },
        { e: "", message: "r4", str_upload: "", file: "" },
        { e: "", message: "g4", str_upload: "", file: "" },
        { e: "", message: "b4", str_upload: "", file: "" }
      ],
      str_namefarm: "",
      str_variety: "",
      str_upload: "",
      str_sampledata: "",
      str_sample1: "",
      str_sample2: "",
      str_result: "",
      result: [],
      note: "",
      uploadPercentage: 0,
      animate: true,
      disUpload: true,
      uploading: false
    };
  },
  mounted() {
    this.setlanguage();
    this.$store.watch(this.$store.getters.getLG, language => {
      this.setlanguage();
    });
    this.clickGet();
  },
  watch: {
    items: {
      handler(val) {
        this.checkfile();
      },
      deep: true
    }
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
    this.interval = setInterval(() => this.getstatus(), 1000);
  },
  methods: {
    getstatus() {
      if (this.animate) {
        axios
          .get(this.$store.state.api + "farms")
          .then(response => {
            var data = response.data.data;
            for (let i = 0; i < data.length; i++) {
              const id = data[i]._id;
              var found = false;
              for (let j = 0; j < this.id_farm_old.length; j++) {
                if (this.id_farm_old[j] == id) {
                  found = true;
                  break;
                }
              }
              if (found) continue;
              this.str_result = response.data.data[i].status;
            }
          })
          .catch(e => {});
      }
    },
    next(web) {
      window.location.href = web;
      // this.$router.push('Home')
    },
    handleResize() {
      this.width = window.innerWidth;
    },
    checkfile() {
      var uploaded = true;
      for (let i = 0; i < 5; i++) {
        uploaded = uploaded && !(this.items[i].file == "");
      }
      if (uploaded) this.disUpload = false;
      else this.disUpload = true;
    },
    setlanguage() {
      var lg = this.$store.state.language;
      if (lg == "TH") {
        this.str_namefarm = "ชื่อแปลง";
        this.str_variety = "ชนิดแปลง";
        this.items[0].str_upload = "Red";
        this.items[1].str_upload = "Green";
        this.items[2].str_upload = "Blue";
        this.items[3].str_upload = "NIR";
        this.items[4].str_upload = "Red Edge";
        this.items[5].str_upload = "DSM";
        this.items[6].str_upload = "DTM";
        this.items[7].str_upload = "Red4M";
        this.items[8].str_upload = "Green4M";
        this.items[9].str_upload = "Blue4M";
        this.str_upload = "อัพโหลดรูปภาพ";
        this.str_sampledata = "ตัวอย่างข้อมูล";
        this.str_sample1 = "KK3";
        this.str_sample2 = "UT82";
        this.note = "หมายเหตุ: ";
      } else if (lg == "ENG") {
        this.str_namefarm = "Name farm";
        this.str_variety = "Variety";
        this.items[0].str_upload = "Red";
        this.items[1].str_upload = "Green";
        this.items[2].str_upload = "Blue";
        this.items[3].str_upload = "NIR";
        this.items[4].str_upload = "Red Edge";
        this.items[5].str_upload = "DSM";
        this.items[6].str_upload = "DTM";
        this.items[7].str_upload = "Red4M";
        this.items[8].str_upload = "Green4M";
        this.items[9].str_upload = "Blue4M";
        this.str_upload = "Upload image";
        this.str_sampledata = "Sample data";
        this.str_sample1 = "KK3";
        this.str_sample2 = "UT82";
        this.note = "Note: ";
      }
    },
    handleFileUpload(index) {
      this.items[index].e = this.$refs.items[index].files[0].name;
      // this.items[0].e = this.$refs.file.files[0].name;
      // this.file = this.$refs.file.files[0];
      this.items[index].file = this.$refs.items[index].files[0];
    },
    setUploading() {
      var str = "";
      var lg = this.$store.state.language;
      if (lg == "TH") {
        str = "กำลังอัปโหลดรูปภาพ...";
      } else if (lg == "ENG") {
        str = "Uploading...";
      }
      this.str_result = str;
      this.animate = true;
    },
    clickUpload() {
      this.setUploading();
      const formData = new FormData();
      formData.append("name", this.name_farm);
      formData.append("variety", this.variety);
      for (let i = 0; i < this.items.length; i++) {
        formData.append(this.items[i].message, this.items[i].file);
      }
      axios
        .post(this.$store.state.api + "farms", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          },
          onUploadProgress: function(progressEvent) {
            this.showProgress(progressEvent);
          }.bind(this)
        })
        .then(response => {
          this.showResult(response.data.msg);
          this.pushResult(response.data.data);
          this.toBottom();
        })
        .catch(e => {});
    },
    showProgress(progressEvent) {
      this.uploadPercentage = (progressEvent.loaded * 80) / progressEvent.total;
    },
    test1() {
      this.setUploading();
      this.uploadPercentage = 80;
      axios
        .post(this.$store.state.api + "test")
        .then(response => {
          this.showResult(response.data.msg);
          this.pushResult(response.data.data);
          this.toBottom();
        })
        .catch(e => {});
    },
    test2() {
      this.$router.push("farm/1569306884042");
    },
    clickGet() {
      axios
        .get(this.$store.state.api + "farms")
        .then(response => {
          this.showResult(response.data.msg);
          this.pushResult(response.data.data);
        })
        .catch(e => {});
    },
    clickDelete() {
      axios
        .delete(this.$store.state.api + "farms")
        .then(response => {
          this.showResult(response.data.msg);
          this.pushResult(response.data.data);
        })
        .catch(e => {});
    },
    clickDeleteFarm(id_farm) {
      axios
        .delete(this.$store.state.api + "farms/" + id_farm)
        .then(response => {
          this.showResult(response.data.msg);
          this.pushResult(response.data.data);
        })
        .catch(e => {});
    },
    pushResult(data) {
      this.result = [];
      this.id_farm_old = [];
      for (let i = 0; i < data.length; i++) {
        this.id_farm_old.push(data[i]._id);
        this.result.push({
          _id: data[i]._id,
          name: data[i].name,
          variety: data[i].variety,
          farm: data[i].farm,
          rgb: data[i].rgb,
          status: data[i].status,
          delete: data[i]._id
        });
      }
      // this.result = data;
    },
    showResult(d) {
      this.uploading = false;
      this.uploadPercentage = 100;
      this.animate = false;
      this.str_result = d;
      this.uploadPercentage = 0;
    },
    toBottom() {
      var that = this;
      setTimeout(function() {
        var scrollingElement = document.scrollingElement || document.body;
        scrollingElement.scrollTop = scrollingElement.scrollHeight;
      }, 10);
    }
  }
};
</script>

<style>
input[type="file"] {
  display: none;
}
.custom-file-upload {
